@import "../styles/core/variables";

.wrapper {
  background-color: rgb(141, 194, 141);
}

.searchBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0.5rem;
  max-width: $max-page-width;

  &_search {
    width: 100%;
    height: 2rem;
    margin-right: 0.75rem;
    padding-left: 0.75rem;
    border: solid 1px gray;
  }
}
